import { Injectable } from "@angular/core";
import { Dictionary } from "@common/Types/Dictionary";

@Injectable()
export class TourTypeService{
    private tourTypes: Dictionary = new Dictionary();

    constructor() {
        this.FillTourTypes();
    }

    private FillTourTypes(){
        this.tourTypes.add('beach', ' ');
        this.tourTypes.add('corporate', ' ');
        this.tourTypes.add('individual', ' ');
        this.tourTypes.add('newYear', ' ');
        this.tourTypes.add('skiing', ' ');
    }

    public GetTourTypes(){
      return this.tourTypes;
    }
}



